.home-container {
text-align: center;
padding: 20px;
background-color: #f5f5f5;
}
 

.hero-section {
background: url("https://gramiyum.in/wp-content/uploads/2025/01/MIllets-Banner.jpg") 
center/cover repeat;
min-height: 220px;
display: flex;
justify-content: center;
align-items: center;
color: white;
text-align: center;
padding: 100px 20px;
border-radius: 10px;
box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.hero-content h2 {
font-size: 60px;
margin-bottom: 10px;
text-align: center;
background-color: green;
margin-left: 20px;
}
.hero-content p {
font-size: 18px;
margin-bottom: 20px;
}
.shop-now-btn {
display: inline-block;
background-color: #ff6f00;
color: white;
padding: 12px 20px;
border-radius: 5px;
text-decoration: none;
font-size: 20px;
font-weight: bold;
transition: background 0.3s ease-in-out;

}
.shop-now-btn:hover {
background-color: #e65c00;
}
.featured-products {
margin-top: 50px;
}
.featured-products h3 {
font-size: 24px;
margin-bottom: 20px;
color: #333;
}
.product-grid {
display: grid;
grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
gap: 20px;
justify-content: center;
padding: 10px;
}
.product-card {
background: white;
border-radius: 10px;
box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
padding: 15px;
transition: transform 0.3s ease-in-out;
text-align: center;
}
.product-card:hover {
transform: translateY(-5px);
}
.product-image {
width: 100%;
height: auto;
border-radius: 10px;
}
.product-price {
font-size: 16px;
color: #27ae60;
font-weight: bold;
margin: 5px 0;
}
.product-description {
font-size: 14px;
color: #666;
margin-bottom: 10px;
}
.view-details-btn {
display: inline-block;
background-color: #222;
color: white;
padding: 8px 15px;
border-radius: 5px;
text-decoration: none;
font-size: 14px;
font-weight: bold;
transition: background 0.3s ease-in-out;
}
.view-details-btn:hover {
background-color: #444;
}
.add-product-container {
max-width: 100%;
margin: 40px auto;
padding: 20px;
background: #ffffff;
border-radius: 10px;
box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
text-align: center;
}
.add-product-container h2 {
color: #333;
margin-bottom: 20px;
}
.add-product-container input {
width: 25%;
padding: 10px;
margin: 10px 0;
border: 1px solid #ddd;
border-radius: 5px;
font-size: 16px;
}
.add-product-container button {
background: #ff5722;
color: white;
padding: 12px;
border: none;
width: 200px;
border-radius: 5px;
font-size: 18px;
cursor: pointer;
transition: 0.3s ease;
}
.add-product-container button:hover {
background: #e64a19;
}
.product-grid {
display: flex;
grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
gap: 20px;
margin-top: 20px;
}
.cart-container {
width: 600px;
height: 100%;
margin: 20px auto;
padding: 20px;
background: linear-gradient(135deg, #ff5722, #ff9a9e);
border-radius: 12px;
box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
text-align: center;
font-family: 'Arial', sans-serif;
}
.cart-container h2 {
color: #333;
margin-bottom: 15px;
font-size: 1.8rem;
}
.cart-list {
list-style: none;
padding: 0;
}
.cart-item {
display: flex;
justify-content: space-between;
align-items: center;
background: #fff;
padding: 10px 15px;
margin: 10px 0;
border-radius: 8px;
box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
transition: transform 0.2s ease-in-out;
width: 30px;
height: 100px;
}
.cart-item img{
width: 150px;
height: 120px;
}
.cart-item:hover {
transform: scale(1.02);
}
.cart-item button {
background: #ff5722;
color: #fff;
border: none;
padding: 6px 12px;
font-size: 0.9rem;
border-radius: 5px;
cursor: pointer;
transition: background 0.3s ease-in-out;
}
.cart-item button:hover {
background: #ff5722;
}
.cart-container p {
font-size: 1.2rem;
color: #555;
margin-top: 10px;
}
.view-product-container {
width: 90%;
max-width: 1200px;
margin: 30px auto;
text-align: center;
font-family: 'Poppins', sans-serif;
}
.view-product-container h2 {
font-size: 2rem;
color: #222;
margin-bottom: 20px;
}
.no-products {
font-size: 1.2rem;
color: #777;
}
.product-grid {
display: grid;
grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
gap: 20px;
justify-content: center;
}
.product-card {
background: #fff;
padding: 15px;
border-radius: 12px;
box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
text-align: center;
}
.product-card:hover {
transform: translateY(-5px);
box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}
.product-image {
width: 100%;
max-height: 160px;
object-fit: cover;
border-radius: 8px;
margin-bottom: 10px;
}
.product-name {
font-size: 1.2rem;
color: #333;
margin: 10px 0;
}
.product-price {
font-size: 1rem;
font-weight: bold;
color: #ff5722;
}
.add-to-cart-btn {
background: linear-gradient(135deg, #ff5722, #ff5722);
color: white;
border: none;
padding: 8px 12px;
font-size: 1rem;
border-radius: 6px;
cursor: pointer;
transition: background 0.3s ease-in-out;
}
.add-to-cart-btn:hover {
background: linear-gradient(135deg, #ff5722, #ff5722);
}

.footer-basic {
  padding:40px 0;
  background-color:#ffffff;
  color:#4b4c4d;
}

.footer-basic ul {
  padding:0;
  list-style:none;
  text-align:center;
  font-size:18px;
  line-height:1.6;
  margin-bottom:0;
}

.footer-basic li {
  padding:0 10px;
}

.footer-basic ul a {
  color:inherit;
  text-decoration:none;
  opacity:0.8;
}

.footer-basic ul a:hover {
  opacity:1;
}

.footer-basic .social {
  text-align:center;
  padding-bottom:25px;
}

.footer-basic .social > a {
  font-size:24px;
  width:40px;
  height:40px;
  line-height:40px;
  display:inline-block;
  text-align:center;
  border-radius:50%;
  border:1px solid #ccc;
  margin:0 8px;
  color:inherit;
  opacity:0.75;
}

.footer-basic .social > a:hover {
  opacity:0.9;
}

.footer-basic .copyright {
  margin-top:15px;
  text-align:center;
  font-size:13px;
  color:#aaa;
  margin-bottom:0;
}

