.navbar {
display: flex;
justify-content: space-between;
align-items: center;
background-color: #000000;
padding: 15px 30px;
box-shadow: 0 4px 8px rgba(128, 23, 23, 0.2);
}
.navbar h1 {
color: #ff6f00;
font-size: 24px;
font-weight: bold;
margin: 0;
}

.navbar div {
display: flex;
gap: 20px;
}
.navbar a {
color: #fff;
text-decoration: none;
font-size: 16px;
font-weight: bold;
transition: color 0.3s ease-in-out;
padding: 5px 10px;
border-radius: 5px;
}
.navbar a:hover {
background-color: #ff6f00;
color: #222;
}